'use client'

import { Avatar, type AvatarProps } from '@mui/material';
import { JSX } from 'react';
import { type AccountData } from '@strife/common';

export interface AccountAvatarProps extends AvatarProps {
  account: AccountData
}

export default function AccountAvatar({ account, ...props }: AccountAvatarProps): JSX.Element {
  const letters = account.username.split(/\s+/)
    .map((value: string): string => value.charAt(0))
    .join();
  return <Avatar {...props}>{letters}</Avatar>;
}
